let autocomplete = {};

autocomplete.createDOM = function () {
    let element = new CreateElement;
    element.create('header', 'div').id('autocomplete-weather').class('autocomplete-weather').position(0);
        element.create('autocomplete-weather', 'div').id('pac-container').class('pac-container').position(0);
            element.create('pac-container', 'input').id('pac-input').class('pac-input').type('text').placeholder('Enter your location').position();
    element.create('article', 'div').id('map').class('map').position();
};

autocomplete.initMap = function () {
    let map = new google.maps.Map(document.getElementById('map'), {
        center: {lat: +window.localStorage.getItem('lat') || 6.047978, lng: +window.localStorage.getItem('lng') || 80.212836},
        zoom: 10,
        mapTypeId: 'hybrid'
        });
    document.getElementById('pac-input').value = window.localStorage.getItem('city') || `Galle, Sri-Lanka`;

    let input = document.getElementById('pac-input');
    let autocomplete = new google.maps.places.Autocomplete(input);
    autocomplete.bindTo('bounds', map);

    let infowindow = new google.maps.InfoWindow();
    let infowindowContent = document.getElementById('infowindow-content');
    infowindow.setContent(infowindowContent);
    let marker = new google.maps.Marker({
        map: map,
        anchorPoint: new google.maps.Point(0, -29)
    });

    autocomplete.addListener('place_changed', function() {
        infowindow.close();
        marker.setVisible(false);
        let place = autocomplete.getPlace();

        map.fitBounds(place.geometry.viewport);
        marker.setPosition(place.geometry.location);
        marker.setVisible(true);
        window.localStorage.setItem('lat', map.center.lat());
        window.localStorage.setItem('lng', map.center.lng());
        window.localStorage.setItem('city', document.getElementById('pac-input').value);
    });
};

autocomplete.turnOn = function () {
    this.createDOM();
    this.initMap();
};

autocomplete.turnOn();
let weather = {};

weather.createDOM = function () {
    let element = new CreateElement;
    element.create('autocomplete-weather', 'div').id('weather-container').class('weather-container').position(1);
        element.create('weather-container', 'div').id('weather-icon').class('weather-icon').position(0);
        element.create('weather-container', 'div').id('weather-temp').class('weather-temp').position(1);
};

weather.getWeather = function () {
    let link = `https://api.openweathermap.org/data/2.5/weather?`;
    let lat = `lat=${parseInt(window.localStorage.getItem('lat') || 6)}`;
    let lon = `lon=${parseInt(window.localStorage.getItem('lon') || 80)}`;
    let api = `APPID=0ad56c244434d4b3ec1cacb5dd73578c`;

    fetch(`${link}${lat}&${lon}&${api}&units=metric`)
        .then(
            response => {
                return response.json();
            }
        )
        .then(
            weather => {
                document.getElementById('weather-icon').innerHTML = `<img src='https://openweathermap.org/img/w/${weather.weather[0].icon}.png' alt='weather icon' id="weather-image">`;
                document.getElementById('weather-temp').innerHTML = `${parseInt(weather.main.temp)}C`;
            }
        )
        .catch(
            (error) => {
                console.log(error);
            }
        );
};

weather.refreshWeather = function () {
    let self = this;
    document.getElementById('pac-input').addEventListener('change', () => {
        self.getWeather();
    });
};

weather.turnOn = function () {
    this.createDOM();
    this.getWeather();
    this.refreshWeather();
};

weather.turnOn();